import request from '@/utils/request'

/**
 * 获取账户列表
 */
export function getAccountList() {
  return request({
    url: 'thirdpay/embed/anpay/main/list',
    method: 'post'
  })
}

/**
 * 获取商编说有信息
 */
export function getAccountInfo(code) {
  return request({
    url: 'thirdpay/embed/anpay/main/get',
    method: 'post',
    data: { code }
  })
}

/**
 * 获取账户余额和可提现金额
 */
export function getMoney(code) {
  return request({
    url: 'thirdpay/embed/anpay/main/moneys',
    method: 'post',
    data: { code }
  })
}

/**
 * 充值下单
 */
export function recharge(data) {
  return request({
    url: 'thirdpay/embed/anpay/recharge/order',
    method: 'post',
    data
  })
}
/**
 * 手机验证码获取
 */
export function getYzm(phone){
  return request({
    url:'external/sms/'+phone,
    method:'get'
  })
}
/**
 * 查询提现信息(包括企业联系人手机号、可提现金额等) --- 企业付款列表查询
 */
export function txInfo(code){
  return request({
    url:'thirdpay/embed/anpay/account/settle/'+code,
    method: 'get'
  })
}
/**
 * 立即提现接口 -- 立即企业付款
 */
export function toWithdraw(params){
  return request({
    url:'thirdpay/embed/anpay/account/settle',
    method: 'post',
    data:params
  })
}
/**
 * 提现到账时间预估
 */
export function aboutTime(){
  return request({
    url:'thirdpay/embed/anpay/settle/estimate-payment-time'
  })
}
/**
 * 查询银行卡名称
 */
export function searchBank(){
  return request({
    url:'dic/thirdpay/anpay/bank',
    method: 'get'
  })
}